.hero{
    width: 100%;
    height: 100vh;
    background: url('https://images.pexels.com/photos/6936981/pexels-photo-6936981.jpeg?auto=compress&cs=tinysrgb&w=1600')no-repeat center center/cover;
}

.hero .container{
    max-width: 1240px;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

.hero p{
    font-size: 1.5rem;
}

.hero .content{
    border-left: 10px solid #000;
    padding-left: 2rem;
    margin-bottom: 15%;
}

.hero .btn{
    margin: 1rem 1rem 1rem 0;
    width: 200px;
}